  --color-grey-10: #F4F4F4;
  --color-yellow: #ffd200;
  --color-blue: #6695a4;
  --font-normal: 'Montserrat', sans-serif, 'PT Sans', "Helvetica Neue", Helvetica, Verdana, Geneva, Arial, sans-serif;
  --border-radius: 8px;
  --max-width: 160rem;
  --max-width-large: 180rem;
  --opacity-half: 0.5;
  --hero-scale: 1;
  --easeInOutCubic: cubic-bezier(0.215, 0.61, 0.355, 1);
}
/* ----------------------------------------
  UTILITY CLASSES
---------------------------------------- */
.u-full {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.u-pf {
  position: fixed;
}
.u-pa {
  position: absolute;
}
.u-tl {
  top: 0;
  left: 0;
}
.u-br {
  bottom: 0;
  right: 0;
}
.u-object-fit {
  -o-object-fit: cover;
     object-fit: cover;
}
.u-cover {
  background-size: cover;
}
.u-contain {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50%;
}
.u-cover--center {
  background-position: 50%;
}
.u-flex {
